.tickets {
    width: 100%;
    fill: var(--grayscale-bg, #F7F8FC);

    section {
        width: 100%;
        margin-top: 30px;

        .section_wrapper {
            max-width: 1122px;
            margin: auto;
            border-radius: 8px;
            border: 1px solid var(--grayscale-divider, #DFE0EB);
            background: var(--grayscale-white, #FFF);

            .title {
                width: 100%;
                padding: 20px 20px;
                margin-bottom: 20px;

                .card1 {
                    width: 100%;

                    h2 {
                        color: var(--grayscale-black, #252733);
                        font-family: Mulish;
                        font-size: 19px;
                        font-style: normal;
                        font-weight: 700;
                        line-height: normal;
                        letter-spacing: 0.4px;
                    }
                }


            }

            .Edits {
                width: 100%;
                padding: 2rem 1rem;

                form {
                    width: 100%;
                    display: flex;
                    gap: 1rem;

                    input {
                        width: 40%;
                        height: 40px;
                        border: none;
                        border-bottom: 1px solid grey;
                        border-radius: 10px;
                        padding: 8px;
                        outline: none;
                        color: var(--grayscale-black, #252733);
                        font-family: Mulish;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: 20px;
                        letter-spacing: 0.2px;
                    }

                    button {
                        width: 20%;
                        height: 40px;
                        border: none;
                        color: var(--grayscale-black, #252733);
                        border-radius: 10px;
                        font-family: Mulish;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: 20px;
                        letter-spacing: 0.2px;
                        cursor: pointer;
                    }
                }
            }

            .dates {
                width: 100%;
                padding: 1rem;
                display: flex;
                justify-content: space-between;

                p {
                    color: var(--grayscale-gray, #9FA2B4);
                    font-family: Mulish;
                    font-size: 14px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    letter-spacing: 0.2px;
                }
            }

            .students {
                width: 100%;
                margin-bottom: 10px;

                .card {
                    width: 100%;
                    padding: 1rem;
                    display: flex;
                    justify-content: space-between;
                    border: 1px solid #DFE0EB;


                    .card1 {
                        width: 33%;
                        display: flex;
                        gap: 5px;

                        h4 {
                            color: var(--grayscale-black, #252733);
                            font-family: Mulish;
                            font-size: 20px;
                            font-style: normal;
                            font-weight: 600;
                            line-height: 20px;
                            letter-spacing: 0.2px;
                        }
                    }

                    .card2 {
                        width: 33%;
                        text-align: center;

                        h4 {
                            color: var(--grayscale-black, #252733);
                            font-family: Mulish;
                            font-size: 20px;
                            font-style: normal;
                            font-weight: 600;
                            line-height: 20px;
                            letter-spacing: 0.2px;
                        }
                    }

                    .card3 {
                        width: 33%;
                        text-align: end;

                        h4 {
                            color: var(--grayscale-black, #252733);
                            font-family: Mulish;
                            font-size: 20px;
                            font-style: normal;
                            font-weight: 600;
                            line-height: 20px;
                            letter-spacing: 0.2px;
                        }
                    }

                }
            }
        }
    }
}