* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  display: flex;
}

.App aside {
  position: fixed;
}
.App .content {
  width: 82%;
  min-height: 100vh;
  position: relative;
  left: 18%;
}

.App .content.dark{
  background: linear-gradient(to right, rgb(93, 93, 93), black );
  color: white;
}

.App .content .header {
  width: 100%;
}
.App .content .header header {
  width: 100%;
  padding: 1rem 0 1rem 0;
}
.App .content .header header nav {
  max-width: 1122px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.App .content .header header nav ul {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 1rem;
}
.App .content .header header nav ul li h1 {
  font-family: Mulish;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.3px;
}

.App .content .header header nav ul li button{
  width: 100px;
  height: 30px;
  border-radius: 5px;
  background: rgb(208, 206, 206);
  border: none;
}

.App .content .header header nav ul li button.dark{
  background: black;
  color: white;
}

.App .content .header header nav ul li select {
  width: 50px;
  height: 30px;
  background: rgb(208, 206, 206);
  border-radius: 5px;
  border: none;
  outline: none;
}

.App .content .header header nav ul li select.dark {
  background: black;
  color: white;
}