section{
    width: 100%;

    .section_wrapper{
        max-width: 1122px;
        margin: auto;
        border-radius: 8px;
        border: 1px solid var(--grayscale-divider, #DFE0EB);
        

        form{
            width: 100%;
            padding: 1rem;
            

            input{
                width: 80%;
                margin: 20px;
            }

            textarea{
                width: 80%;
                display: block;
            }

            
        }
    }
}