.list_item {
    display: block;
    padding: 15px;
    display: flex;
    gap: 10px;
    text-decoration: none;
    color: #A4A6B3;
    font-family: Mulish;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.2px;
    border-left: 2px solid #363740;

    svg {
        fill: #9FA2B4;
    }

    &.active{
        background: #9fa2b423;
        border-left:2px solid white;
        color:  #DDE2FF;

        svg{
            fill:  white;
        }
    }
}