.overview {
    width: 100%;
    fill: var(--grayscale-bg, #F7F8FC);

   

    
    main {
        width: 100%;

        .main_wrapper {
            max-width: 1122px;
            margin: auto;
            display: flex;
            justify-content: space-between;
            padding: 1rem 0;
            gap: 1rem;

            .card {
                width: 258px;
                border-radius: 8px;
                border: 1px solid var(--grayscale-divider, #DFE0EB);
                background: var(--grayscale-white, #FFF);
                padding: 20px;

                p {
                    color: var(--grayscale-gray, #9FA2B4);
                    text-align: center;
                    font-family: Mulish;
                    font-size: 19px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    letter-spacing: 0.4px;
                }

                h1 {
                    color: var(--grayscale-black, #252733);
                    text-align: center;
                    font-family: Mulish;
                    font-size: 40px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    letter-spacing: 1px;
                }
            }

            .card:hover {
                border: 1px solid var(--accent-default, #3751FF);
                cursor: pointer;

                p {
                    color: #3751FF;
                }

                h1 {
                    color: #3751FF;
                }
            }
        }
    }

    section {
        width: 100%;

        .section_wrapper {
            max-width: 1122px;
            margin: auto;
            display: flex;
            border-radius: 8px;
            border: 1px solid var(--grayscale-divider, #DFE0EB);
            background: var(--grayscale-white, #FFF);


            .wrapper1 {
                width: 70%;
                padding: 1rem;

                h2 {
                    color: var(--grayscale-black, #252733);
                    font-family: Mulish;
                    font-size: 19px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    letter-spacing: 0.4px;
                }

                .wrapper_card {
                    width: 100%;
                    display: flex;

                    .card1 {
                        width: 50%;

                        p {
                            color: var(--grayscale-gray, #9FA2B4);
                            font-family: Mulish;
                            font-size: 12px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 16px;
                            letter-spacing: 0.1px;
                            padding: 10px 0;
                        }
                    }

                    .card2 {
                        width: 50%;
                        text-align: right;


                        p {
                            display: inline;
                            color: var(--grayscale-gray, #9FA2B4);
                            font-family: Mulish;
                            font-size: 12px;
                            font-style: normal;
                            font-weight: 400;
                            line-height: 16px;
                            letter-spacing: 0.1px;
                            padding: 10px 0;
                        }
                    }
                }

            }

            .wrapper2 {
                width: 30%;

                .card {
                    width: 100%;
                    text-align: center;
                    padding: 2rem;
                    border: 1px solid var(--grayscale-divider, #DFE0EB);


                    p {
                        color: var(--grayscale-gray, #9FA2B4);
                        text-align: center;
                        font-family: Mulish;
                        font-size: 16px;
                        font-style: normal;
                        font-weight: 600;
                        line-height: 22px;
                        letter-spacing: 0.3px;
                        padding-bottom: 10px;
                    }

                    h2 {
                        color: var(--grayscale-black, #252733);
                        text-align: center;
                        font-family: Mulish;
                        font-size: 24px;
                        font-style: normal;
                        font-weight: 700;
                        line-height: normal;
                        letter-spacing: 0.3px;
                    }
                }
            }
        }
    }
}